'use client'

import React, { useState, useEffect } from 'react'
import { useAuth } from '@/contexts/AuthContext'

export const AuthDebug: React.FC = () => {
  const { user, token, isLoading } = useAuth()
  const [localStorageStatus, setLocalStorageStatus] = useState('N/A')

  useEffect(() => {
    // Vérifier le localStorage côté client uniquement
    if (typeof window !== 'undefined') {
      const token = localStorage.getItem('access_token')
      setLocalStorageStatus(token ? 'present' : 'missing')
    }
  }, [])

  // Ne s'affiche qu'en mode développement
  if (process.env.NODE_ENV !== 'development') {
    return null
  }

  return (
    <div className="fixed bottom-4 right-4 bg-black/80 text-white p-4 rounded-lg text-xs max-w-sm z-50">
      <h3 className="font-bold mb-2">Auth Debug</h3>
      <div>Loading: {isLoading ? 'true' : 'false'}</div>
      <div>User: {user ? JSON.stringify(user, null, 2) : 'null'}</div>
      <div>Token: {token ? `${token.substring(0, 20)}...` : 'null'}</div>
      <div>LocalStorage: {localStorageStatus}</div>
    </div>
  )
}
